import React from 'react';
import { Col, Container, Row, Button } from 'react-bootstrap';
import { FaPaw, FaMoneyCheckAlt, FaChartLine, FaTable } from 'react-icons/fa';
import mainimg from './phone 3.png';

import './styles.css';

const AboutUs = () => {
    return (
        <div id="aboutus" className='hero'>
            <div className="right-tri">
            <Container className='heroContainer'>
                <Row>
                    <Col>
                    <div className='heroButtonDiv'>
                          About Us
                        </div>
                        <div className='heroText'>
                            We are a group of ambitious students looking to improve safety and peace of mind for our community, and eventually the world.

                    
                        </div>
                        
                    </Col>
                    <Col>
                    <img
                        alt=""
                        src={mainimg}
                        width="383"
                        height="658"
                        className="heroPic"
                    />{' '}
                    </Col>
                </Row>
            </Container>  
            </div>
            
        </div>
    )
}

export default AboutUs
