import React from 'react'
import './style.css'
import image1 from './img/gps.png'
import image2 from './img/simple.png'
import image3 from './img/voice.png'

const Features = () => {
    


    return (
        <div id='features' class="page"> 
            <div class="center">
            <h2>Features</h2>
            </div>
            
            
            
              
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/materialize/1.0.0-beta/css/materialize.min.css"></link>
            <div class="row">
                <div class="col s12 m4">   
                    <div class="card">
                        <div class="card-title center" >
                            GPS Support
                        </div>
                        <div class="card-content">
                            Location immediatly sent out and periodically updated to all of your emergency contacts.
                        </div>
                        <div class="card-image center">
                        <img src={image1} style={{height: "200px", maxWidth: "200px", marginLeft: "25%", marginright: "25%"}}/>
                        </div>
                    </div>
                        
                </div>
                <div class="col s12 m4">   
                    <div class="card">
                        <div class="card-title center">
                            Voice Recognition
                        </div>
                        <div class="card-content">
                            Activate all safety features using your voice and a code word that only you would know.
                        </div>
                        <div class="card-image">
                        <img src={image3} style={{height: "200px", maxWidth: "200px", marginLeft: "25%", marginright: "25%"}}/>
                        </div>
                    </div>
                        
                </div>
                <div class="col s12 m4">   
                    <div class="card">
                        <div class="card-title center">
                            Simplistic Design
                        </div>
                        <div class="card-content">
                            App design is very simple to allow fast and easy access to all features.
                        </div>
                        <div class="card-image">
                        <img src={image2} style={{height: "190px", maxWidth: "250px", marginLeft: "20%", marginright: "25%"}}/>
                        </div>
                    </div>
                </div>
            </div>            


        </div>
    )
}

export default Features