import React from 'react'
import './style.css'
import image from './img/panicphone.png'

const TheApp = () => {
    


    return (
        <div id='theapp' class="page2"> 

                   

            <div class="row">

                <div class="col s12 m6">
                    <div class="image1">
                        <img src={image}/>
                    </div>
                    
                </div>
                     
                
                <div class="col s12 m6">
                    <div class="right">
                        <h2 className='theapptitle'>The App</h2>
                        <h4>Allegiant allows you to send vital information to your emergency contacts in discrete manner.
                        <br/>
                        <br />
                        We support a plethora of ways to trigger the emergency contact signal, giving you unique options for any situation.
                        </h4>
                    </div> 
                </div>
                 
            </div>


        </div>
    )
}

export default TheApp