import React from 'react';
import { Col, Container, Row, Button } from 'react-bootstrap';
import { FaPaw, FaMoneyCheckAlt, FaChartLine, FaTable } from 'react-icons/fa';
import mainimg from './phone 2.png';

import './styles.css';

const Landing = () => {
    return (
        <div id="hero" className='hero'>
            <div className="right-tri">
            <Container className='heroContainer'>
                <Row>
                    <Col>
                        <div className='landingText'>
                        Allegiant

                        </div>
                        <div className='landingButtonDiv'>
                        With state of the art voice tracking technology, you can keep yourself safe at all times.

                        </div>
                    </Col>
                    <Col>
                    <img
                        alt=""
                        src={mainimg}
                        width="383"
                        height="658"
                        className="heroPic"
                    />{' '}
                    </Col>
                </Row>
            </Container>  
            </div>
            
        </div>
    )
}

export default Landing
