import React from 'react'
import { Link, animateScroll as scroll } from "react-scroll";
import Navbar from 'react-bootstrap/Navbar'
import { Nav, Button } from 'react-bootstrap'
import logo from './Image 1@1X.png';
import './navbar.css'

const Navigation = () => {
    return (
        <>
            <Navbar sticky="top" expand="lg">
                <Navbar.Brand href="#home">
                    <Link
                        activeClass="active"
                        to="hero"
                        spy={true}
                        smooth={true}
                        duration={500}
                        className="scroll scroll-title"
                    > 
                    <img
                            alt=""
                            src={logo}
                            width="211"
                            height="50"
                            className="d-inline-block align-top"
                        />{' '}
                    </Link>
                </Navbar.Brand>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ml-auto">
                        <Button className="mr-4 navbutton">
                            <Link
                                activeClass="active"
                                to="aboutus"
                                spy={true}
                                smooth={true}
                                duration={500}
                                className="scroll"
                            >
                                About Us
                            </Link>
                        </Button>
                        <Button className="mr-4 navbutton">
                            <Link
                                activeClass="active"
                                to="features"
                                spy={true}
                                smooth={true}
                                duration={500}
                                className="scroll"
                            >
                                Features
                            </Link>
                        
                        </Button>
                        <Button className="mr-4 navbutton">
                            <Link
                                activeClass="active"
                                to="theapp"
                                spy={true}
                                smooth={true}
                                duration={500}
                                className="scroll"
                            >
                                The App
                            </Link>
                        </Button>
                        {/* <Button className="mr-4 navbutton">
                            <Link
                                activeClass="active"
                                to="contact"
                                spy={true}
                                smooth={true}
                                duration={500}
                                className="scroll"
                            >
                                Contact
                            </Link>
                        </Button> */}
                        <Button className="mr-4 navbutton">
                            <Link
                                activeClass="active"
                                to="contact"
                                spy={true}
                                smooth={true}
                                duration={500}
                                className="scroll"
                            >
                                Download
                            </Link>
                        </Button>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>   
        </>
    )
}

export default Navigation
